$(function () {
    $('.cases__search svg').on('click', function (e) {
        $('.cases__search-form').fadeToggle();
    });

    $(document).mouseup(function (e) { //    -
        var div = $(".cases__search"); //   ID 
        if (!div.is(e.target) //       
            &&
            div.has(e.target).length === 0) { //      
            $('.cases__search-form').fadeOut(); //  
        }
    });
});